@font-face {
  font-display: swap;
  font-family: "Inter-Regular";
  src: url("../../fonts/Inter-Regular.woff2") format("woff2"), url("../../fonts/Inter-Regular.ttf") format("truetype"), url("../../fonts/Inter-Regular.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Inter-SemiBold";
  src: url("../../fonts/Inter-SemiBold.woff2") format("woff2"), url("../../fonts/Inter-SemiBold.ttf") format("truetype"), url("../../fonts/Inter-SemiBold.otf") format("opentype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Caveat-Bold";
  src: url("../../fonts/Caveat-SemiBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body, html {
  background: var(--white);
  color: var(--black);
  font-family: var(--font-family);
  font-size: var(--c-font-size);
  line-height: 1.4;
  font-weight: 400;
  margin: 0 auto;
  -webkit-text-size-adjust: 100%;
}

body {
  font-size: var(--c-font-size);
  letter-spacing: 0.45px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1:first-child, h2:first-child, h3:first-child, h4:first-child, h5:first-child, h6:first-child, p:first-child, ul:first-child, ol:first-child, li:first-child, blockquote:first-child, tt:first-child, mark:first-child,
form:first-child, table:first-child, a:first-child, .button:first-child {
  margin-top: 0 !important;
}
h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child, ul:last-child, ol:last-child, li:last-child, blockquote:last-child, tt:last-child, mark:last-child,
form:last-child, table:last-child, a:last-child, .button:last-child {
  margin-bottom: 0 !important;
}

a {
  text-decoration: none;
  transition: all ease-in-out 0.2s;
}

html {
  --c-padding: 0;
  --c-font-size: 14px;
  --cols: 12;
  --gutter: 20px;
  --column: calc((100% - 20px * (12 - 1)) / 12);
  --w-content: 960px;
  --h-header: 48px;
  --font-family: "Inter-Regular", sans-serif;
  --font-family-b: "Inter-SemiBold", sans-serif;
  --font-family-c: "Caveat-Bold", sans-serif;
  --white: #fff;
  --black: #282827;
  --gray: #747473;
  --gray-2: #929292;
  --gray-3: #d4d4d4;
  --line-light: #f5f5f5;
  --bg-light: #fafafa;
  --bg-light-2: #f9f9f9;
  --yellow-main: #f8ca02;
  --yellow-secondary: #ffdf65;
  --orange: #ffc165;
  --orange-2: #f1711c;
  --green: #56e264;
  --pro-grey-pro: #a8a8a8;
  --pro-pro-lines: #232322;
  --pro-lines-bg-pro: #151515;
  --successful: #49aa53;
  --violet: #7165ff;
  --toner: rgba(0, 0, 0, 0.25);
  --toner-160: rgba(0, 0, 0, 0.16);
}
@media (max-width: 640px) {
  html {
    --c-padding: 20px;
  }
}

.overflow {
  overflow: hidden;
}

.hidden {
  display: none !important;
}
@media (max-width: 640px) {
  .hidden--mob {
    display: none !important;
  }
}
@media (min-width: 640px) and (max-width: 1200px) {
  .hidden--tab {
    display: none !important;
  }
}
@media (min-width: 1200px) {
  .hidden--desktop {
    display: none !important;
  }
}

.section {
  width: 100%;
  max-width: 100%;
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}
.section__ui {
  padding: 40px 0;
}
.section__wrapper {
  max-width: var(--w-content);
  margin: 0 auto;
  padding: var(--c-padding);
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .section__wrapper {
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .section__wrapper {
    padding: 20px;
  }
}
@media (max-width: 1200px) {
  .section__wrapper {
    padding: var(--c-padding);
  }
}
@media (max-width: 1500px) {
  .section__wrapper {
    padding: var(--c-padding);
  }
}
@media (min-width: 320px) {
  .section__wrapper {
    width: 100%;
    padding: 20px;
  }
}
@media (max-width: 2560px) {
  .section__wrapper {
    width: 100%;
    padding: var(--c-padding);
  }
}
.section__header-mt {
  margin-top: var(--h-header);
}
.section h1, .section h2,
.section .t-h1_h2 {
  font-weight: 600;
  font-size: 52px;
  line-height: 116%;
  letter-spacing: -0.01em;
}
.section h3,
.section .t-h3 {
  font-weight: 600;
  font-size: 32px;
  line-height: 112%;
  letter-spacing: -0.01em;
}
.section h4,
.section .t-m {
  font-weight: 600;
  font-size: 22px;
  line-height: 127%;
}
.section h5,
.section .t-s {
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
}
.section p {
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
}
.section ol, .section ul {
  margin-left: 30px;
}
.section ol li, .section ul li {
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}
.flex.row {
  flex-direction: row;
}
.flex.col {
  flex-direction: column;
}
.flex > * {
  flex: 1 1 auto;
  margin-top: 0;
}

.media {
  display: flex;
  overflow: hidden;
  position: relative;
}
.media--bg {
  background: var(--bg-light);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.media--bg img, .media--bg video, .media--bg iframe {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.media--img {
  background: var(--bg-light-2);
}

@media (min-width: 640px) {
  .mobile-only {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .mobile-plus-tablet {
    display: none !important;
  }
}

@media (max-width: 640px) {
  .desktop-plus-tablet {
    display: none !important;
  }
}

.button {
  position: relative;
  display: flex;
  align-content: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
  text-decoration: none;
  border-radius: 16px;
  padding: 8px 10px 8px 10px;
  width: auto;
  transition: all linear 0.2s;
}
.button.disabled, .button[disabled] {
  pointer-events: none;
  opacity: 0.25;
}
.button--white {
  border-width: 1px;
  border-style: solid;
  border-color: var(--white);
  color: var(--white);
  background: rgba(0, 0, 0, 0);
}
.button--white:hover {
  color: var(--yellow-main);
  border: 1px solid var(--yellow-main);
}
.button--yellow {
  color: var(--black);
  background: var(--yellow-secondary);
}
.button--yellow:hover {
  background: var(--yellow-main);
}

.language-link {
  position: relative;
  display: inline-flex;
  align-items: center;
  margin-right: auto;
  cursor: pointer;
  text-decoration: none;
  min-width: 40px;
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
  color: var(--white);
}
.language-link:hover {
  color: var(--yellow-main);
}
.language-link__icon {
  display: inline-block;
  margin-left: 12px;
  width: 6px;
  height: 4px;
  background: var(--yellow-secondary);
  mask-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='6' height='4' viewBox='0 0 6 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M3.32822 3.46667C3.12822 3.73333 2.72822 3.73333 2.52822 3.46667L0.528223 0.799999C0.281009 0.470381 0.5162 -5.32739e-07 0.928223 -4.96719e-07L4.92822 -1.47027e-07C5.34025 -1.11007e-07 5.57544 0.470382 5.32822 0.8L3.32822 3.46667Z' fill='%23FFDF65'/%3e%3c/svg%3e");
  mask-size: contain;
  mask-repeat: no-repeat;
  transition: transform 0.3s ease;
}
.language-link__icon.is-active {
  transform: rotate(90deg);
}
.language-link__dropdown {
  display: none;
  position: absolute;
  background: var(--pro-lines-bg-pro);
  list-style: none;
  margin: 0 0 0 -10px;
  padding: 5px 10px;
  border-radius: 0 0 4px 4px;
  z-index: 3;
}
.language-link__dropdown a {
  color: var(--white);
}
.language-link__dropdown a:hover {
  color: var(--yellow-secondary);
}

.promo-links {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  gap: 16px;
  margin-top: 32px;
}
.promo-links__item {
  display: flex;
  justify-content: "flex-start";
  align-items: "flex-start";
  flex-direction: column;
}
.promo-links__item h3 {
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
  color: var(--gray);
  margin-bottom: 8px;
}
.promo-links__item ul {
  list-style: none;
}
.promo-links__item ul li {
  margin-bottom: 8px;
}
.promo-links__item ul li a {
  display: flex;
  justify-content: "flex-start";
  align-items: "center";
  color: var(--white);
}
.promo-links__item ul li a:hover {
  color: var(--yellow-secondary);
}
.promo-links__item ul li.current-menu-item a {
  color: var(--yellow-main);
}
.promo-links__mt {
  margin-top: 16px;
}
.promo-links__directions ul li a, .promo-links__catalog ul li a, .promo-links__company ul li a {
  font-weight: 400;
  font-size: 13px;
  line-height: 140%;
}
.promo-links__social {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.promo-links__social a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
}
.promo-links__social a svg {
  width: 16px;
  height: 16px;
  min-width: 100%;
  fill: var(--yellow-secondary);
  transition: all ease-in-out 0.2s;
}
.promo-links__social a:hover svg {
  fill: var(--white);
}

.tag {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 18px;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  padding: 0 8px;
  margin-left: 4px;
  font-weight: 600;
  font-size: 9px;
  line-height: 120%;
}
.tag--discount {
  color: var(--white);
  border-color: var(--orange-2);
  background: var(--orange-2);
}
.tag--gift {
  color: var(--orange);
  border-color: var(--orange);
}
.tag--soon {
  color: var(--violet);
  border-color: var(--violet);
}
.tag--new {
  color: var(--green);
  border-color: var(--green);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--h-header);
  backdrop-filter: blur(20px);
  background: #181818;
  z-index: 9999;
}
.header__wrapper {
  height: var(--h-header);
  display: flex;
  justify-content: "flex-start";
  align-items: center;
  max-width: var(--w-content);
  margin: 0 auto;
  padding: var(--c-padding);
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .header__wrapper {
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .header__wrapper {
    padding: 20px;
  }
}
@media (max-width: 1200px) {
