    padding: var(--c-padding);
  }
}
@media (max-width: 1500px) {
  .header__wrapper {
    padding: var(--c-padding);
  }
}
@media (min-width: 320px) {
  .header__wrapper {
    width: 100%;
    padding: 20px;
  }
}
@media (max-width: 2560px) {
  .header__wrapper {
    width: 100%;
    padding: var(--c-padding);
  }
}
.header__wrapper--full {
  height: var(--h-header);
  display: flex;
  justify-content: "flex-start";
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .header__wrapper--full {
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .header__wrapper--full {
    padding: 20px;
  }
}
@media (max-width: 1200px) {
  .header__wrapper--full {
    padding: 20px;
  }
}
@media (max-width: 1500px) {
  .header__wrapper--full {
    padding: 20px;
  }
}
@media (min-width: 320px) {
  .header__wrapper--full {
    width: 100%;
    padding: 20px;
  }
}
@media (max-width: 2560px) {
  .header__wrapper--full {
    width: 100%;
    padding: 20px;
  }
}
.header__wrapper--navigation {
  display: flex;
  justify-content: "flex-start";
  align-items: center;
  flex-direction: column;
  max-width: var(--w-content);
  margin: 0 auto;
  padding: var(--c-padding);
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .header__wrapper--navigation {
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .header__wrapper--navigation {
    padding: 20px;
  }
}
@media (max-width: 1200px) {
  .header__wrapper--navigation {
    padding: var(--c-padding);
  }
}
@media (max-width: 1500px) {
  .header__wrapper--navigation {
    padding: var(--c-padding);
  }
}
@media (min-width: 320px) {
  .header__wrapper--navigation {
    width: 100%;
    padding: 20px;
  }
}
@media (max-width: 2560px) {
  .header__wrapper--navigation {
    width: 100%;
    padding: var(--c-padding);
  }
}
@media (max-width: 450px) {
  .header__wrapper--navigation {
    padding: 0 20px;
  }
}
.header__logo {
  height: 32px;
  margin-right: 37px;
}
.header__logo img {
  width: 100%;
  height: auto;
}
.header .sandwich {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 46px;
  padding: 8.5px 0;
  cursor: pointer;
}
.header .sandwich span {
  display: block;
  width: 32px;
  height: 2px;
  border-radius: 2px;
  background: #fff;
  margin: 0 auto 3px;
  transition: all ease-in-out 0.2s;
}
.header .sandwich.active span:nth-child(1) {
  transform: translateY(5px) rotate(15deg);
}
.header .sandwich.active span:nth-child(2) {
  opacity: 0;
  filter: alpha(opacity=0);
}
.header .sandwich.active span:nth-child(3) {
  transform: translateY(-5px) rotate(-15deg);
}
.header__navigation {
  display: none;
  position: relative;
  z-index: 2;
  width: 100%;
  height: calc(100vh - var(--h-header));
  background: var(--pro-lines-bg-pro);
  overflow-y: auto;
}
.header__navigation ul.promo-links__directions li a {
  font-size: 17px;
  line-height: 22px;
  font-weight: 600;
}
.header__navigation ul.promo-links__catalog li a {
  font-size: 13px;
  line-height: 140%;
  font-weight: 400;
}
.header__call {
  margin-right: 8px;
}
.header__catalog {
  margin-right: 16px;
  padding: 8px 13px 8px 12px;
}
.header__cart {
  position: relative;
  z-index: 1;
}
.header__cart svg {
  width: 28px;
  height: 24px;
  fill: var(--white);
}
.header__cart--count {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 0.6px;
  min-width: 13px;
  max-height: 13px;
  font-weight: 700;
  font-size: 9px;
  font-family: var(--font-family-b);
  border-radius: 100%;
  color: var(--black);
  background-color: var(--yellow-secondary);
  z-index: 2;
}
.header .button {
  height: 32px;
}

.footer {
  width: 100%;
  padding: 64px 0 12px;
  background: #0f0f0f;
}
.footer__wrapper {
  display: flex;
  justify-content: "flex-start";
  align-items: center;
  flex-direction: column;
  max-width: var(--w-content);
  margin: 0 auto;
  padding: var(--c-padding);
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .footer__wrapper {
    padding: 20px;
  }
}
@media (max-width: 640px) {
