.footer .feedback {
  width: 100%;
  padding-bottom: 27px;
  border-bottom: 1px solid #1d1d1d;
  display: flex;
  justify-content: "space-between";
  align-items: center;
}
.footer .feedback__text {
  font-weight: 600;
  font-size: 17px;
  line-height: 129%;
  background: linear-gradient(90deg, var(--gray) 0%, var(--orange) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
@media (max-width: 640px) {
  .footer .feedback__button .button {
    font-size: 15px;
    height: 48px;
  }
}
@media (max-width: 450px) {
  .footer .feedback__button .button {
    font-size: 15px;
    height: 48px;
  }
}
@media (max-width: 640px) {
  .footer .feedback__button {
    margin-top: 20px;
  }
}
@media (max-width: 450px) {
  .footer .feedback__button {
    margin-top: 20px;
  }
}
@media (max-width: 640px) {
  .footer .feedback {
    padding-bottom: 20px;
    flex-wrap: wrap;
    align-items: center;
  }
}
@media (max-width: 450px) {
  .footer .feedback {
    padding-bottom: 20px;
    flex-wrap: wrap;
    align-items: center;
  }
}
.footer .breadcrumbs {
  width: 100%;
  margin-top: 17px;
  display: flex;
  justify-content: "flex-start";
  align-items: center;
}
.footer .breadcrumbs a {
  font-weight: 500;
  font-size: 10px;
  color: var(--gray);
}
.footer .breadcrumbs a:hover {
  color: var(--yellow-secondary);
  text-decoration: underline;
}
.footer .breadcrumbs span {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 16px;
  width: 13.29px;
  height: 1px;
  background-color: var(--pro-pro-lines);
