}
.footer__info {
  display: flex;
  justify-content: "flex-start";
  align-items: "flex-start";
  width: 100%;
  font-weight: 400;
  font-size: 10px;
  line-height: 140%;
  color: var(--gray);
  margin-top: 64px;
  padding-bottom: 12px;
  border-bottom: 1px solid #1d1d1d;
}
.footer__copyright {
  display: flex;
  justify-content: "space-between";
  align-items: "flex-start";
  width: 100%;
  margin-top: 12px;
}
.footer__copyright p {
  font-weight: 400;
  font-size: 10px;
  line-height: 140%;
  color: var(--gray);
}
.footer__copyright p:last-child {
  margin-left: 20px;
}
.footer__copyright p a {
  text-decoration: underline;
  color: var(--gray);
}
.footer__copyright p a:hover {
  color: var(--yellow-secondary);
}